.card-date {
}

.card-date__month, .card-date__year {
    width: 50%;
    float: left;
}

.card-date__month {
    padding-right: 15px;
}

.card-date__year {
    padding-left: 15px;
}

.show-error-after-submit .ng-invalid:not(.no-style-error) {
    .card-date__year:not(.card-date_selected), .card-date__month:not(.card-date_selected) {
        .form-control{
            border: 0px solid #ed272f;
            outline: 0;
            box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075), 0 0 12px #ed272f;
            -moz-box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075), 0 0 8px #ed272f;
            -webkit-box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075), 0 0 8px #ed272f;
        }
    } 

    
}
