.animate-opacity-show {
    .crossAnimate {
        -webkit-transition: 400ms cubic-bezier(0.250, 0.250, 0.750, 0.750) all;
        -moz-transition: 400ms cubic-bezier(0.250, 0.250, 0.750, 0.750) all;
        -ms-transition: 400ms cubic-bezier(0.250, 0.250, 0.750, 0.750) all;
        -o-transition: 400ms cubic-bezier(0.250, 0.250, 0.750, 0.750) all;
        transition: 400ms cubic-bezier(0.250, 0.250, 0.750, 0.750) all;
    }

    .crossTransform(@opacity) {
        opacity: @opacity;
    }

    &.ng-hide-remove {
        .crossAnimate();
        .crossTransform(0);
        &.ng-hide-remove-active {
            .crossTransform(1);
        }
    }

}

.animate-scale {
    .crossAnimate {
        -webkit-transition: 300ms cubic-bezier(0.250, 0.250, 0.750, 0.750) all;
        -moz-transition: 300ms cubic-bezier(0.250, 0.250, 0.750, 0.750) all;
        -ms-transition: 300ms cubic-bezier(0.250, 0.250, 0.750, 0.750) all;
        -o-transition: 300ms cubic-bezier(0.250, 0.250, 0.750, 0.750) all;
        transition: 300ms cubic-bezier(0.250, 0.250, 0.750, 0.750) all;
        position: relative;
        display: block;
    }

    .crossTransform(@index) {
        -webkit-transform: scaleY(@index);
        -moz-transform: scaleY(@index);
        -ms-transform: scaleY(@index);
        -o-transform: scaleY(@index);
        transform: scaleY(@index);
        opacity: @index;
    }

    &.ng-enter:not(.fake-block) {
        .crossAnimate();
        .crossTransform(0);

        &.ng-enter-active {
            .crossTransform(1);
        }
    }

    &.ng-leave:not(.fake-block) {
        .crossAnimate();
        .crossTransform(1);

        &.ng-leave-active {
            .crossTransform(0);
        }
    }
}
