.animate-yo-yo {
    .ng-enter {
        -webkit-animation: enter 600ms cubic-bezier(0.445, 0.050, 0.550, 0.950);
        animation: enter 600ms cubic-bezier(0.445, 0.050, 0.550, 0.950);
        display: block;
        position: relative;
    }

    @-webkit-keyframes enter {
        from {
            opacity: 0;
            height: 0px;
            left: -70px;
        }

        75% {
            left: 15px;
        }

        to {
            opacity: 1;
            height: 30px;
            left: 0px;
        }
    }

    @keyframes enter {
        from {
            opacity: 0;
            height: 0px;
            left: -70px;
        }

        75% {
            left: 15px;
        }

        to {
            opacity: 1;
            height: 30px;
            left: 0px;
        }
    }

    .ng-leave {
        -webkit-animation: leave 600ms cubic-bezier(0.445, 0.050, 0.550, 0.950);
        animation: leave 600ms cubic-bezier(0.445, 0.050, 0.550, 0.950);
        display: block;
        position: relative;
    }

    @-webkit-keyframes leave {
        to {
            opacity: 0;
            height: 0px;
            left: -70px;
        }

        25% {
            left: 15px;
        }

        from {
            opacity: 1;
            height: 30px;
            left: 0px;
        }
    }

    @keyframes leave {
        to {
            opacity: 0;
            height: 0px;
            left: -70px;
        }

        25% {
            left: 15px;
        }

        from {
            opacity: 1;
            height: 30px;
            left: 0px;
        }
    }
}
