@tooltip-text-color: #2a2a2a;
@tooltip-bg-color: #e9eff2;

@tooltip-text-error-color: #ffffff;
@tooltip-bg-error-color: #ab4445;

.tooltip {
  position: absolute;
  z-index: 1070;
  display: block;
  visibility: visible;
  font-size: 12px;
  font-family: robotoregular;
  line-height: 1.4;
  .opacity(0);

  &.in     { .opacity(1); }
  &.top    { margin-top:  -3px; padding: 5px 0; }
  &.right  { margin-left:  3px; padding: 0 5px; }
  &.bottom { margin-top:   3px; padding: 5px 0; }
  &.left   { margin-left: -3px; padding: 0 5px; }

  &.fade {
    .transition(opacity 0.15s linear 0s);

    &.in {
         .transition(opacity 0.15s linear 0s);
    }
}
}

// Wrapper for the tooltip content
.tooltip-inner {
  max-width: 200px;
  padding: 10px 30px; 
  text-align: center;
  text-decoration: none;
  border-radius: 4px;
}

// Arrows
.tooltip-arrow {
  position: absolute;
  width: 0;
  height: 0;
  border-color: transparent;
  border-style: solid;
}

.tooltip {
    &.top .tooltip-arrow {
        bottom: 0;
        left: 50%;
        margin-left: -5px;
        border-width: 5px 5px 0;
    }

    &.top-left .tooltip-arrow {
        bottom: 0;
        left: 5px;
        border-width: 5px 5px 0;
    }

    &.top-right .tooltip-arrow {
        bottom: 0;
        right: 5px;
        border-width: 5px 5px 0;
    }

    &.right .tooltip-arrow {
        top: 50%;
        left: 0;
        margin-top: -5px;
        border-width: 5px 5px 5px 0;
    }

    &.left .tooltip-arrow {
        top: 50%;
        right: 0;
        margin-top: -5px;
        border-width: 5px 0 5px 5px;
    }

    &.bottom .tooltip-arrow {
        top: 0;
        left: 50%;
        margin-left: -5px;
        border-width: 0 5px 5px;
    }

    &.bottom-left .tooltip-arrow {
        top: 0;
        left: 5px;
        border-width: 0 5px 5px;
    }

    &.bottom-right .tooltip-arrow {
        top: 0;
        right: 5px;
        border-width: 0 5px 5px;
    }

    .setArrowColor(@tooltip-bg-color);
    .setWrapperColor(@tooltip-bg-color, @tooltip-text-color);

    &.error {
        .setArrowColor(@tooltip-bg-error-color);
        .setWrapperColor(@tooltip-bg-error-color, @tooltip-text-error-color);
    }
}

.setWrapperColor(@bgColor, @textColor) {
    .tooltip-inner {
        color: @textColor;
        background-color: @bgColor;
    }
}

.setArrowColor(@bgColor) {
        &.top .tooltip-arrow {
            border-top-color: @bgColor;
        }

        &.top-left .tooltip-arrow {
            border-top-color: @bgColor;
        }

        &.top-right .tooltip-arrow {
            border-top-color: @bgColor;
        }

        &.right .tooltip-arrow {
            border-right-color: @bgColor;
        }

        &.left .tooltip-arrow {
            border-left-color: @bgColor;
        }

        &.bottom .tooltip-arrow {
            border-bottom-color: @bgColor;
        }

        &.bottom-left .tooltip-arrow {
            border-bottom-color: @bgColor;
        }

        &.bottom-right .tooltip-arrow {
            border-bottom-color: @bgColor;
        }
}