

@table-border-radius: 4px;
/*tabs*/
@tabs-top-active-color: #ff5e1d;
@tabs-active-color: #ffffff;
@tabs-border-color: #d0d0d0;
@tabs-background-color: #f4f4f4;
@filter-border-color: #dedede;
@sort-desc: #4cb400;
@default-text-color: #606060;
@default-background-color: #2D2D33;
@border-top-color: #dedede;
@table-border-color: #ddd;
@table-tr-text-color: #e55322;
@table-pagination-color: @table-tr-text-color;
@table-overlay-color: #ffffff;
@table-header-background: #D6E4CD;
@table-pagination-active-color: @table-header-background;
@table-pagination-active-text-color: @default-text-color;


@table-header-top-color: #008DCE;
@table-header-bottom-color: #00557F;
@table-header-text-color: #ffffff;
@table-odd-color: #eeeeee;
@table-even-color: #e0e0e0;
@table-header-background-green: #D6E4CD;
/*Table*/
.table-wrapper {
    width: 100%;
    display: table;

    .generate-columns(@n, @step, @i: 1) when (@i =< @n) {
        @namePrefix: (@i * @step);
        @stepPercent: (@step + 0%);

        .col-width-@{namePrefix} {
            width: (@i * @stepPercent);
        }

        .generate-columns(@n, @step, (@i + 1));
    }

    .generate-columns(4, 1);
    .generate-columns(10, 5);

    .table-holder {
        display: table-cell;
        width: 80%;
    }

    .filter-holder {
        display: table-cell;
        width: 40%;
    }

    .filter-btn-holder {
        display: table;
        margin-top: 15px;
        padding: 0 5px;

        .filter-btn-holder {
            display: table-cell;
            margin: 10px;
        }
    }
}

.quick-filter-wrapper {
    margin: 0 20px;
    border: 2px solid @filter-border-color;

    .filter-title {
        color: #f05110;
        background: #eeeeee;
        font-weight: bold;
        text-align: center;
        padding: 9px;
        margin-bottom: 5px;
    }
}

.quick-filter-holder {
    overflow-x: hidden;
    overflow-y: auto;
    max-height: 600px;

    input[type="text"] {
        width: 100%;
    }

    .filter-item-holder {
        padding: 5px 10px;
        //margin: 10px 0;
    }
}

table {

    &.table-main {
        tr {
            th {
                padding: 5px;
                font-size: 14px;
                letter-spacing: 0;
                //color: @table-header-text-color;
                //.linear-gradient(@table-header-top-color, @table-header-bottom-color);
                //border-left: 1px solid @border-top-color;
                &:nth-child(odd) {
                    //border-bottom: 2px solid @border-top-color;
                }

                &:first-child {
                    border-left: 0;
                }

                &.sortable {
                    cursor: pointer;

                    div {
                        padding-right: 18px;
                        position: relative;

                        &:after, &:before {
                            content: "";
                            border-width: 0 4px 4px;
                            border-style: solid;
                            border-color: @sort-desc transparent;
                            visibility: visible;
                            right: 8px;
                            top: 50%;
                            position: absolute;
                            .opacity(0.8);
                            margin-top: -4px;
                        }

                        &:before {
                            margin-top: 2px;
                            border-bottom: none;
                            border-left: 4px solid transparent;
                            border-right: 4px solid transparent;
                            border-top: 4px solid @sort-desc;
                        }
                    }

                    div:hover:after, div:hover:before {
                        opacity: 1;
                        visibility: visible;
                    }

                    &.sort-desc, &.sort-asc {
                        background-color: #edf5e8;

                        div:after {
                            margin-top: -2px;
                        }

                        div:before {
                            visibility: hidden;
                        }
                    }

                    &.sort-asc div:after, &.sort-asc div:hover:after {
                        visibility: visible;
                        .opacity(0.8);
                    }

                    &.sort-desc div:after {
                        border-bottom: none;
                        border-left: 4px solid transparent;
                        border-right: 4px solid transparent;
                        border-top: 4px solid @sort-desc;
                        visibility: visible;
                        .opacity(0.8);
                    }
                }
            }

            td {
                padding: 5px;
                //border-left: 1px solid @border-top-color;
                &:first-child {
                    border-left: 0;
                }

                &:nth-child(odd) {
                    //border-bottom: 1px solid #dedede;
                }
            }

            &.odd {
                background-color: @table-odd-color;
            }

            &.even {
                background-color: @table-even-color;
            }
        }
    }
}

ul.pagination {
    list-style: none;
    padding: 0;
    margin: 20px 0;

    li {
        display: inline-block;
        margin: 0;
        padding: 0;

        a {
            padding: 6px 12px;
            display: block;
            color: @table-pagination-color;
        }

        border-top: 1px solid @border-top-color;
        border-bottom: 1px solid @border-top-color;
        border-left: 1px solid @border-top-color;
        border-right: 1px solid @border-top-color;
        margin-top: -1px;
        margin-left: -1px;

        &.disabled a {
            cursor: default;
            color: @default-text-color;
        }

        &:last-child {
            border-top-right-radius: @table-border-radius;
            border-bottom-right-radius: @table-border-radius;
        }

        &:first-child {
            border-top-left-radius: @table-border-radius;
            border-bottom-left-radius: @table-border-radius;
        }

        &.active {
            background-color: @table-pagination-active-color;
            color: @table-pagination-active-text-color;
        }
    }
}

.loading-container {
    position: relative;

    .loading {
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        min-height: 50px;
        min-width: 50px;
        display: block;
        background-color: @table-overlay-color;
        .opacity(0.5);
    }
}

.control-btn-holder {
    margin-bottom: 10px;

    button {
        display: inline-block;
        margin-right: 15px;
    }
}
