.modal-header {
    h3 {
        font: normal 24px robotoregular;
        color: #5b7382;
        margin: 10px 0;
    }
}
.modal-body {
    .row {
        margin-top: 20px;
        margin-bottom: 20px;
    }

    .left-label {
        text-align: right;
        padding: 5px 0;
        font: normal 14px robotoregular;
    }

    .right-label {
        text-align: left;
        padding: 5px 0;
    }
    .table-responsive {
        .animate-opacity()
    }
}
