@btn-color: #555;
@color-placeholder: #d6d6d6;
@time-opacity-placeholder: 0.4s;
@default-green-color: #74cbc8;
@default-blue: #56a8dd;
@default-disabled-btn: #b7cfd3;

.base-icon {
    text-decoration: none;
    color: #cedae2;
    font-size: 26px;
}
.text-success {
    color: #3c763d !important;
}
.text-error {
    color: #a94442 !important;
}
.cursor-move {
    cursor: move;
}
.alert {
    margin-bottom: 0;
}

.form-control {
    height: 38px;
    border-radius: 0px;
    box-shadow: none;
    border-color: #c7dcdf;
    font-family: robotoregular;

    &.italic {
        font-family: robotoitalic;
    }
}
.text-first-uppercase:first-letter {
    .text-uppercase();
}

textarea.form-control {
    min-height: 110px !important;
}

a {
    cursor: pointer;
    text-decoration: none;

    &.dashed {
        border-bottom: 1px dashed #66bab7;
    }

    &:hover {
        text-decoration: none;
    }
}

.nav {
    margin-left: 5px;
}

.custom-hard-toggle {
    width: 110px;
    float: right;
    margin-left: 5px;
    height: 40px;
    margin-bottom: 0;
}

.switch-light > span span {
    top: 7px;
}

.switch-candy {
    background-color: initial;
    border-radius: initial;
    color: initial;
    font-weight: initial;
    text-align: initial;
    text-shadow: initial;
    box-shadow: initial;

    a {
        background-color: #56a8dd;
        border: initial;
        border-radius: initial;
    }
}
.tags {
    padding: 9px 12px;
    border-radius: 2px !important;
    .form-control() !important;
    border: none !important;
    height: 100% !important;
    padding: 4px 8px !important;

    &:after {
        border-color: #c7dcdf transparent transparent transparent;
    }

    &.input-active {
        &:after {
            border-color: transparent transparent #c7dcdf transparent;
        }
    }

    .input {
        padding-left: 0 !important;
        font: inherit !important;
    }
}

.base-tag {
    padding: 5px 10px;
    height: auto;
    line-height: 20px;
    margin: 0 2px;
    border: 0;
}
.padding-left {
    padding-left: 0;
}

.tags .tag-item {
    .base-tag() !important;
    background: @default-green-color !important;
    color: #fff !important;
    border-radius: 2px !important;
}

tags-input .host {
    margin: 0 !important;
}

.tag-traveler {
    .tag-item {
        .base-tag() !important;
        color: #838c92 !important;
        background: #eaeaea !important;
        border-radius: 10px !important;
    }

    .customTagItem {
        .base-tag() !important;
        color: #838c92 !important;
        background: #eaeaea !important;
        border-radius: 10px !important;
    }


    .errorTagItem {
        .base-tag() !important;
        color: #838c92 !important;
        background: #ffcccc !important;        
        border-radius: 10px !important;
    }

    .choose-item {
        line-height: 30px !important;
    }
}

.container {
    width: 100%;
}

.btn-default {
    background: none;
    box-shadow: none;
    border-radius: 0;
    padding: 0 10px;
    font: normal 16px/36px robotoregular;
    position: relative;
    height: 40px;
    border: 2px solid #c9dee1;
    border-radius: 3px;
    color: #b7cfd3;
    background: none;

    &:hover {
        border-color: #56a8dd;
        color: #56a8dd;
        background: none;
    }
}

.icon {
    cursor: pointer;
}

.padding {
    padding: 0 !important;
}

.padding-right {
    padding-right: 0 !important;
}

.padding-div div {
    padding: 0 !important;
}

.margin {
    margin: 0 !important;
}

.all-row-margin-left-right {
    .row {
        .margin-left-right()
    }
}

.margin-top {
    margin-top: 0 !important;
}

.margin-bottom {
    margin-bottom: 0 !important;
}

.margin-top-bottom {
    margin-top: 0 !important;
    margin-bottom: 0 !important;
}

.margin-left-right {
    margin-left: 0 !important;
    margin-right: 0 !important;
}

.vertical-align {
    display: flex;
    align-items: center;
}

.word-wrap {
    word-wrap: break-word;
}

.modal {
    overflow-y: auto;
}

.btn-default {
    border-color: @default-blue;
    color: @default-blue;
    opacity: 0.7;
    transition: all 0.2s linear;

    &:hover {
        opacity: 1;
    }

    &:disabled {
        color: @default-disabled-btn;
    }
}
