@import '../../../../less/controls/variables.less';
@pageMainColor: @BorderColor;

.ath-pagination {
    .ath-pagination__block {
        display: inline-block;
        background-color: white;
        transition: all 0.2s ease-out;

        &:hover {
            background: @pageMainColor;

            .ath-pagination-link__content {
                color: white;
            }
        }
    }

    .ath-pagination__selected {
        background: @pageMainColor;

        .ath-pagination-link__content {
            color: white;
        }
    }

    .ath-pagination__link {
    }
}

.ath-pagination-link__content {
    padding: 5px 10px;
    border: 1px solid @pageMainColor;
    color: @pageMainColor;
}

.ath-pagination-previous {
}

.ath-pagination-body {
    margin-right: 5px;
}

.ath-pagination-next {
}

.ath-pagination-next,
.ath-pagination-previous {
    &.disabled {
        .ath-pagination-link__content {
            color: @disabled-color;
            border-color: @disabled-color;
            cursor: @cursor-disabled;
        }

        &:hover {
            background-color: transparent;

            .ath-pagination-link__content {
                color: @disabled-color;
            }
        }
    }
}
