@btn-important-background-color: none;

.btn-important {
    background:  linear-gradient(to left, @ath-color-orangeGradient-0, @ath-color-orangeGradient-1);
    color: white;
    border: none;
    border-radius: 20px;
    &:hover {
        color: white;
    }
    &:focus {
        background:  linear-gradient(to left, @ath-color-orangeGradient-0, @ath-color-orangeGradient-1);
        color: white;
        outline: none !important;
    }
    &.active {
        background:  linear-gradient(to left, @ath-color-blueGradientLight-0, @ath-color-blueGradientLight-1);
    }
}
