@padding-button-left: 1.2em;
@background-button-color: #ffffff;
@background-button-color-control: #c5deec;
@background-button-color-active: #c5deec;
@background-button-color-hover: #0073ea;
@background-text-color-hover: #ffffff;
@padding-button-left-header: 2.2em;
@btn-default-border: 1px solid #dddddd;
@padding-button: 1px;
@datepicker-text-color: #0073ea;
@background-thead: #dddddd;
@text-week: #444444;

.uib-datepicker {
    table {
        outline: none;
    }

    line-height: 14px;
    width: 300px;
    .thead-th-button(@width) {
        width: @width !important;
        line-height: 22px;
        height: 25px;
        border: none;
    }
    .well-sm { 
        padding: 0;
    }
    thead th:nth-child(1) button {
        .thead-th-button(45px);
    }

    thead th:nth-child(2) button {
        .thead-th-button(200px);
    }

    thead th:nth-child(3) button {        
        .thead-th-button(45px);
    }

    thead {
        display: block;
        background: @background-thead;
        padding: 5px;

        tr {
            th {
                small {
                    font-weight: bold;
                    color: @text-week;
                }

                .uib-left {
                    .uib-right-left-title(45px);
                }

                .uib-title {
                    .uib-right-left-title(200px);
                }

                .uib-right {
                    .uib-right-left-title(45px);
                }

                .uib-right-left-title(@width) {
                    width: @width;
                    line-height: 22px;
                    height: 25px;
                    border: none;
                }
            }
        }
    }

    tbody {
        display: inline-block;
        padding: 5px;
        width: 100%;

        tr {
            width: 100%;

            td {
                padding: @padding-button !important;

                .btn-default {
                    border-width: 1px;
                    border-radius: 2px;
                }
            }
        }
    }

    tbody tr td {
        em {
            width: 40px;
        }

        .btn-default {
            line-height: 20px;
            height: 25px;
            padding: 0 9px;
        }
    }

    .date-btn-footer {
        padding: 0 6px;

        button {
            display: inline-block;
        }
    }

    .btn-disabled {
        opacity: .35;
        background: @background-button-color;
    }


    .btn-default {
        border: 1px solid #dddddd;
        background: @background-button-color;
        font-weight: bold;
        color: @datepicker-text-color;
        opacity: 0.7;
    }

    .btn-default:hover {
        background: @background-button-color-hover;
        color: @background-text-color-hover;
    }

    .button-bar {
        padding: 0 6px;

        .btn {
            .btn-default();
            border: 1px solid @datepicker-text-color;
            transition: all 0.2s linear;

            &:hover {
                background: @background-button-color-hover;
                color: @background-text-color-hover;
            }
        }
    }

    .active {
        background: @background-button-color-active !important;
        color: @datepicker-text-color !important;
    }
}

.uib-datepicker-popup {
    &.dropdown-menu {
        padding: 0 0 6px;
    }

    li {
        padding: 0;
    }




    .uib-monthpicker tbody tr td {
        .btn-default {
            width: 5.88em;
        }
    }

    .uib-yearpicker tbody tr td {
        .btn-default {
            width: 3.5em;
        }
    }


    .btn-info:active {
        background: @background-button-color-active;
        color: @datepicker-text-color;
    }


    .text-info {
        color: #31708f;
    }
}
