@left-panel-width: 250px;

body {
    font-family: 'robotoregular', Arial, sans-serif;
}
/*loading icon*/
#generalLoader {
	width:            100px;
	height:           100px;
    position: absolute;
    background-repeat: no-repeat;
	background-image: url('data:image/gif;base64,');
    &.center {
        top: 50%;
        left: 50%;
    }
}

a {
    color: #66bab7;

    &.dotted {
        text-decoration: none;
        border-bottom: 2px dotted #cfeced;
    }
}
/// For directive ath-velocity-modal
.ath-dynamic-modal {
    .modal-dialog {
        opacity: 0;
        background: white;
        border-radius: 10px;
    }

    .modal-content {
        opacity: 0;
    }
}