@switchWidth: 30px;
@dateFromToWidth: @switchWidth/2;

.date-interval {
    position: relative;
    padding: 0;

    .switch {
        width: @switchWidth;
        float: left;

        &.icon-transition-date i {
            cursor: pointer;
            padding: 0;
            top: 12px;
            color: #9fb4ba;
        }
    }

    .date-from {
        width: -moz-calc( ~"50% - "@dateFromToWidth);
        width: -webkit-calc( ~"50% - "@dateFromToWidth);
        width: -o-calc( ~"50% - "@dateFromToWidth);
        width: calc( ~"50% - "@dateFromToWidth);
        float: left;
    }

    .date-to {
        width: -moz-calc( ~"50% - "@dateFromToWidth);
        width: -webkit-calc( ~"50% - "@dateFromToWidth);
        width: -o-calc( ~"50% - "@dateFromToWidth);
        width: calc( ~"50% - "@dateFromToWidth);
        float: left;
    }
}
