@charset "UTF-8";

@font-face {
  font-family: "ath-icons";
  src:url("fonts/ath-icons/ath-icons.eot");
  src:url("fonts/ath-icons/ath-icons.eot?#iefix") format("embedded-opentype"),
    url("fonts/ath-icons/ath-icons.woff") format("woff"),
    url("fonts/ath-icons/ath-icons.ttf") format("truetype"),
    url("fonts/ath-icons/ath-icons.svg#ath-icons") format("svg");
  font-weight: normal;
  font-style: normal;
}

[data-icon]:before {
  font-family: "ath-icons" !important;
  content: attr(data-icon);
  font-style: normal !important;
  font-weight: normal !important;
  font-variant: normal !important;
  text-transform: none !important;
  speak: none;
  line-height: 1;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

[class^="icon-"]:before,
[class*=" icon-"]:before {
  font-family: "ath-icons" !important;
  font-style: normal !important;
  font-weight: normal !important;
  font-variant: normal !important;
  text-transform: none !important;
  speak: none;
  line-height: 1;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.icon-2:before {
  content: "a";
}
.icon-3:before {
  content: "b";
}
.icon-4:before {
  content: "c";
}
.icon-5:before {
  content: "d";
}
.icon-6:before {
  content: "e";
}
.icon-7:before {
  content: "f";
}
.icon-8:before {
  content: "g";
}
.icon-wheel:before {
  content: "h";
}
