.ath-table {
}

.ath-table-header__row {
    background-color: #D5EAF5;
}

.ath-table-body {
    .ath-table-body__row {
        transition: background .15s linear;

        &.odd {
            background-color: #f6ffff;
        }

        &.even {
            background-color: #E5F2F4;
        }

        &:hover {
            background-color: #d4e1e3 !important;
            cursor: pointer;
        }
    }
}
