@border-color-content: #d6e0e7;
@background-container: #f4fafb;
@heightFooter: 100px;
@minWidthContentLayout: 680px;

html {
    height: 100%;
    position: relative;
}

body {
    position: relative;
    -webkit-transition: -webkit-transform 0.6s ease-in-out;
    transition: overflow 0.6s ease-in-out;
    -webkit-backface-visibility: hidden;
    backface-visibility: hidden;
}

#content-holder {
    height: 100%;
}

#main-loader {
    height: 100%;
    background: @background-container;
}

.main-container {
    height: 100%;
    position: relative;
}

.dot {
    overflow: hidden;
    text-overflow: ellipsis;
    padding: 0 5px;
}

.dot-dot-dot {
    white-space: nowrap;
    .dot();
}

.dot-dot-dot-no-space {
    .dot();
}

.center {
    text-align: center;
}

.base-icon-after {
    color: #c7dcdf;
    font-family: "ath-icons" !important;
    font-style: normal !important;
    font-weight: normal !important;
    font-variant: normal !important;
    text-transform: none !important;
    speak: none;
    line-height: 1;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    pointer-events: none;
}

.field-wrapper {
    min-height: 38px;
    box-shadow: none;
    border-radius: 2px;
    border: 1px solid #C7DCDF;
}

.field-wrapper-height {
    height: auto;
}

.size-icon {
    font-size: 24px;
}

.pre-wrap {
    white-space: pre-wrap;
}


.left-label {
    font: normal 16px robotoregular;
    color: @text-muted;
    text-align: right;
    padding-top: 5px;
    line-height: 20px;
}
label {
    margin-bottom: 0;
    }

.row {
    margin-top: 20px;
    margin-bottom: 20px;
}

select {
    width: 105%;
}


.select-overflow {
    overflow: hidden;
}



.wide-text {
    line-height: 20px;
}


.common-label {
    font: normal 16px/40px robotoregular;
    color: #838c92;

    &.right {
        text-align: right;
    }
}

.center-label {
    font: normal 16px/40px robotoregular;
    padding-left: 26px;
}

.right-label {
    font: normal 16px/40px robotolight;
    color: #838c92;
}

.person-add .btn {
    width: 100%;
}

.panel-heading {
    padding: 0 15px;

    div {
        display: inline-block;
    }

    .list-person {
        background-color: #eaeaea;
        padding: 0 5px;
        margin-right: 10px;
        border-radius: 10px;
    }

    .person-name {
        font: normal 16px/32px robotoregular;
        padding: 0 5px;

        .icon:before {
            margin-right: 8px;
            position: relative;
            font-size: 16px;
            top: 4px;
        }

        a {
            color: #838c92;
        }
    }

    .person-company {
        font: normal 16px/32px robotolight;
        color: #838c92;
    }
}

.person-actions {
    text-align: right;
    font: normal 12px/32px robotolight;
    margin: 0px -10px;

    .icon {
        display: inline-block;
        margin-left: 25px;
        color: #838c92;

        &:before {
            top: 4px;
        }

        &:first-child {
            margin-left: 0;
        }
    }
}

.content-layout {
    left: 0;
    margin-left: @left-panel-width;
    position: relative;
    padding: 0px 38px;
    border-top: 5px solid #ececec;
    transition: margin-left 0.3s ease-out, left 0.3s ease-out;
    min-width: @minWidthContentLayout;
    height: 100%;

    .row .right {
        text-align: right;
    }

    .icon-traveler {
        .base-icon();
        color: #000;
    }



    .left-panel-visible & {
        left: 0;
        margin-left: @left-panel-width;
    }

    h1 {
        font: normal 28px robotoregular;
        color: #5b7382;
        padding: 30px 0;

        img {
            position: absolute;
            right: 110px;
        }
    }

    .nav-icons {
        position: absolute;
        top: 27px;
        right: 38px;
        text-align: right;
        font-size: 28px;
        color: #d3dee5;

        .icon {
            display: inline-block;
            margin-left: 20px;
        }
    }

    .border-line {
        height: 93px;
        border-radius: 10px;
        border-bottom: 2px solid #d3dee5;
        border-right: 2px solid #d3dee5;
        margin-bottom: 37px;
    }





    .w100 {
        margin-left: -38px;
        margin-right: -38px;
        padding-left: 40px;
        padding-right: 40px;
    }

    .blue-container {
        border-top: 3px solid @border-color-content;
        border-bottom: 1px solid #daedf0;
        background: @background-container;
        position: relative;
        z-index: 11;
        min-height: -moz-calc(~"100% - "@heightFooter);
        min-height: -webkit-calc(~"100% - "@heightFooter);
        min-height: -o-calc(~"100% - "@heightFooter);
        min-height: calc(~"100% - "@heightFooter);
    }





    .person-accordion {
        border: 0;
        box-shadow: none;
        border-top: 1px solid #daedf0;
        border-radius: 0;
        margin-top: 0 !important;
        margin-right: -38px;
        padding-right: 38px;
        background: #ffffff; /* Old browsers */
        background: -moz-linear-gradient(left, #ffffff 0%, #eaf6f8 100%); /* FF3.6+ */
        background: -webkit-gradient(linear, left top, right top, color-stop(0%,#ffffff), color-stop(100%,#eaf6f8)); /* Chrome,Safari4+ */
        background: -webkit-linear-gradient(left, #ffffff 0%,#eaf6f8 100%); /* Chrome10+,Safari5.1+ */
        background: -o-linear-gradient(left, #ffffff 0%,#eaf6f8 100%); /* Opera 11.10+ */
        background: -ms-linear-gradient(left, #ffffff 0%,#eaf6f8 100%); /* IE10+ */
        background: linear-gradient(to right, #ffffff 0%,#eaf6f8 100%); /* W3C */
        filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#ffffff', endColorstr='#eaf6f8',GradientType=1 ); /* IE6-9 */
        &:last-of-type {
            border-bottom: 1px solid #daedf0;
        }



        .panel-body {
            border: 0 !important;
            padding: 0;

            .person-form {
                padding: 0;

                .selectize-input {
                    height: 36px;
                    padding: 2px 12px;
                }

                .left-label {
                    font: normal 14px/36px robotoregular;
                }

                .common-label {
                    font: normal 14px/36px robotoregular;
                }

                .form-control {
                    height: 36px;
                }

                > .row {
                    &:first-child {
                        margin-top: 0;

                        .top-border {
                            border-top: 2px dotted #daedf0;
                            right: 16px;

                            &:before {
                                content: '';
                                height: 2px;
                                position: absolute;
                                width: 100px;
                                background: white;
                                top: -2px;
                                left: -67px;
                            }
                        }
                    }

                    > div:nth-child(2) {
                        padding-left: 50px;
                    }
                }

                .row {
                    margin-top: 0;
                    margin-bottom: 0;
                }

                .radio-group {
                    color: #c3d4d7;

                    input {
                        display: none;
                    }

                    label {
                        display: inline-block;
                        padding: 0 20px;
                        border-radius: 2px;
                        height: 36px;
                        font: normal 14px/36px robotoregular;
                        color: #737f7f;
                        background: #e5f2f4;
                        cursor: pointer;
                        margin-left: 15px;

                        &:first-of-type {
                            margin-left: 0;
                            margin-right: 15px;
                        }
                    }

                    input[type="radio"]:checked + label {
                        color: white;
                        background: @default-green-color;
                    }
                }

                .checkbox-wrapper {
                    input {
                        display: none;
                    }

                    label {
                        font: normal 14px/36px robotoregular;

                        .icon {
                            display: inline-block;
                            width: 20px;
                            height: 20px;
                            position: relative;
                            top: 8px;
                            margin-right: 10px;
                            border-radius: 2px;
                            background: @default-green-color;
                            color: @default-green-color;

                            &:before {
                                position: relative;
                                top: -6px;
                                left: 6px;
                            }
                        }
                    }

                    input:checked + label .icon {
                        color: white;
                    }
                }
            }
        }
    }

    .order-block {
        border-top: 1px solid #daedf0;

        &:first-of-type {
            margin-top: 40px;
        }

        .order-header {
            .order-person {
                font: normal 16px/50px robotoregular;
                color: #666666;

                .img {
                    display: inline-block;
                    border-radius: 12px;
                    margin-right: 10px;
                    overflow: hidden;
                    height: 24px;
                    width: 24px;
                    vertical-align: middle;
                    position: relative;
                    top: -2px;

                    img {
                        vertical-align: top;
                    }
                }
            }

            &.secondary {
                .order-person {
                    font-size: 14px;

                    .img {
                        background: #daedf0;
                        text-align: center;

                        img {
                            position: relative;
                            top: 6px;
                        }
                    }
                }
            }

            .order-status {
                font: normal 12px/50px robotobold_italic;
                color: #cfd5d9;

                .toggle {
                    color: #88b4bc;
                    text-decoration: none;
                    opacity: .5;
                    margin-left: 35px;
                    position: relative;
                    top: 2px;

                    &:hover {
                        opacity: 1;
                    }
                }
            }
        }

        .order-details {
            position: relative;
            top: -12px;

            &:before {
                content: '';
                border-left: 2px solid #daedf0;
                height: 72px;
                bottom: 0;
                left: 25px;
                position: absolute;
            }

            &:last-child {
                &:before {
                    display: none;
                }
            }

            > div {
                height: 80px;
                padding-top: 0;
                border-bottom: 2px solid #daedf0;
                margin-bottom: 50px;

                &:first-child {
                    border-left: 2px solid #daedf0;
                    border-bottom-left-radius: 20px;
                    position: relative;
                    left: 25px;
                }

                &:last-child {
                    position: relative;
                    right: 17px;
                    padding-right: 0;

                    .icon.finish {
                        position: absolute;
                        right: 0;
                        bottom: -19px;
                        font-size: 45px;
                        color: #daedf0;
                    }
                }

                &.empty {
                    border-bottom: 0;
                }

                &.dotted {
                    border-bottom-style: dotted;
                }
            }

            .name {
                position: relative;
                margin-top: 35px;
                font: normal 16px robotobold;
                color: #a7b4b4;

                &:after {
                    content: '';
                    position: absolute;
                    width: 10px;
                    height: 10px;
                    z-index: 20;
                    top: 39px;
                    left: 0;
                    border-radius: 5px;
                    background: #daedf0;
                }

                &.green:after {
                    background: #1db670;
                }

                &.red:after {
                    background: #ed272f;
                }

                &.yellow:after {
                    background: #febe10;
                }

                &.circle:before {
                    content: '';
                    position: absolute;
                    width: 6px;
                    height: 6px;
                    z-index: 21;
                    top: 41px;
                    left: 2px;
                    border-radius: 3px;
                    background: #ecf7f9;
                }
            }

            .dates {
                margin-top: 40px;
                font: normal 12px robotobold;
                color: #c4cece;

                span {
                    margin-right: 20px;
                }

                .icon:before {
                    position: relative;
                    top: 3px;
                    margin-right: 8px;
                    font-size: 16px;
                }
            }

            .status {
                margin-top: 40px;
                font: normal 12px robotobold_italic;
                color: #c4cece;

                &.yellow {
                    color: #febe10;
                }

                &.green {
                    color: #1db670;
                }

                &.red {
                    color: #ed272f;
                }
            }
        }

        &.collapsed {
            background: #fff;
        }

        &.expanded {
            background: #eaf6f8; /* Old browsers */
            background: -moz-linear-gradient(top, #eaf6f8 0%, #ffffff 100%); /* FF3.6+ */
            background: -webkit-gradient(linear, left top, left bottom, color-stop(0%,#eaf6f8), color-stop(100%,#ffffff)); /* Chrome,Safari4+ */
            background: -webkit-linear-gradient(top, #eaf6f8 0%,#ffffff 100%); /* Chrome10+,Safari5.1+ */
            background: -o-linear-gradient(top, #eaf6f8 0%,#ffffff 100%); /* Opera 11.10+ */
            background: -ms-linear-gradient(top, #eaf6f8 0%,#ffffff 100%); /* IE10+ */
            background: linear-gradient(to bottom, #eaf6f8 0%,#ffffff 100%); /* W3C */
            filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#eaf6f8', endColorstr='#ffffff',GradientType=0 ); /* IE6-9 */
            .order-status-header {
                display: none;
            }
        }
    }

    .services-block {
        margin-top: 30px;
        margin-bottom: 30px;

        .services-header {
            .person {
                font: normal 16px/50px robotoregular;
                color: #666666;

                .img {
                    display: inline-block;
                    border-radius: 12px;
                    margin-right: 10px;
                    overflow: hidden;
                    height: 24px;
                    width: 24px;
                    vertical-align: middle;
                    position: relative;
                    top: -2px;

                    img {
                        vertical-align: top;
                    }
                }
            }
        }

        .tabs {
            a {
                position: relative;
                top: 2px;
                line-height: 40px;
                padding: 8px 20px 0;
                margin: 0;
                display: inline-block;
                font-size: 24px;
                text-decoration: none;
                border-top: 1px solid transparent;
                border-left: 1px solid transparent;
                border-right: 1px solid transparent;
                border-bottom: 1px solid transparent;

                &.active {
                    background: white;
                    border-top: 1px solid #daedf0;
                    border-left: 1px solid #daedf0;
                    border-right: 1px solid #daedf0;
                    border-bottom: 1px solid #fff;

                    &:after {
                        content: '';
                        width: 8px;
                        height: 8px;
                        position: absolute;
                        top: 10px;
                        right: 12px;
                        border-radius: 4px;
                        background: #febe10;
                    }
                }

                &.disabled {
                    opacity: .3;
                    cursor: default;
                }

                &:hover {
                    color: #66bab7;
                }

                .icon.custom img {
                    position: relative;
                    top: -3px;
                }
            }
        }

        .actions {
            font: normal 14px/50px robotobold_italic;
            color: #cfd5d9;

            .toggle {
                color: #85b2ba;
                text-decoration: none;
                opacity: .5;
                margin-left: 25px;
                position: relative;
                top: 2px;

                &:hover {
                    opacity: 1;
                }
            }
        }

        .services-body {
            border: 1px solid #daedf0;
            background: #fff;
            padding: 10px 20px;

            > .row {
                margin-left: 0;
                margin-right: 0;

                > div {
                    border-top: 1px solid #f2f2f2;

                    &:first-child {
                        padding-left: 0;
                    }

                    &:last-child {
                        padding-right: 0;
                    }
                }

                &:first-of-type > div {
                    border: 0;
                }
            }

            .service-destination {
                padding: 15px 0;
            }

            .airline {
                font: normal 14px robotobold;
                color: #a7b4b4;

                &.delta:before {
                    content: '';
                    width: 18px;
                    height: 14px;
                    display: inline-block;
                    background: url(http://test/ath/images/delta.png) 0 0 no-repeat;
                    margin-right: 6px;
                    position: relative;
                    top: 1px;
                }

                .service-class {
                    display: inline-block;
                    position: relative;
                    font: normal 12px robotoregular;
                    background: #f2f2f2;
                    margin-left: 10px;
                    padding: 2px 5px;
                }
            }



            .ticket-status,
            .service-status {
                > div {
                    padding: 15px inherit;
                }

                .status {
                    font: normal 14px robotobold;
                    color: #b6c2c2;
                    text-transform: uppercase;

                    &:before {
                        content: '';
                        display: inline-block;
                        width: 16px;
                        height: 15px;
                        position: relative;
                        margin-right: 10px;
                        top: 2px;
                        background: #b6c2c2;
                    }

                    &.success {
                        color: @default-green-color;

                        &:before {
                            background: @default-green-color;
                        }
                    }

                    &.warning {
                        color: #febe10;

                        &:before {
                            background: #febe10;
                        }
                    }
                }
            }

            .service-status {
                .status:before {
                    border-radius: 8px;
                }
            }

            .order-details {
                position: relative;
                top: -12px;

                &:before {
                    content: '';
                    border-left: 2px solid #daedf0;
                    height: 72px;
                    bottom: 0;
                    left: 22px;
                    position: absolute;
                }

                &:last-child {
                    &:before {
                        display: none;
                    }
                }

                > div {
                    height: 80px;
                    padding-top: 0;
                    border-bottom: 2px solid #daedf0;
                    margin-bottom: 50px;

                    &:first-child {
                        border-left: 2px solid #daedf0;
                        border-bottom-left-radius: 20px;
                        position: relative;
                        left: 22px;
                    }

                    &:last-child {
                        position: relative;
                        right: 17px;
                        padding-right: 0;

                        .icon.finish {
                            position: absolute;
                            right: 0;
                            bottom: -19px;
                            font-size: 45px;
                            color: #daedf0;
                        }
                    }

                    &.empty {
                        border-bottom: 0;
                    }

                    &.dotted {
                        border-bottom-style: dotted;
                    }
                }

                .name {
                    position: relative;
                    margin-top: 35px;
                    font: normal 16px robotobold;
                    color: #a7b4b4;

                    &:after {
                        content: '';
                        position: absolute;
                        width: 10px;
                        height: 10px;
                        z-index: 20;
                        top: 39px;
                        left: 0;
                        border-radius: 5px;
                        background: #daedf0;
                    }

                    &.green:after {
                        background: #1db670;
                    }

                    &.red:after {
                        background: #ed272f;
                    }

                    &.yellow:after {
                        background: #febe10;
                    }

                    &.circle:before {
                        content: '';
                        position: absolute;
                        width: 6px;
                        height: 6px;
                        z-index: 21;
                        top: 41px;
                        left: 2px;
                        border-radius: 3px;
                        background: #ecf7f9;
                    }
                }

                .dates {
                    margin-top: 40px;
                    font: normal 12px robotobold;
                    color: #c4cece;

                    span {
                        margin-right: 20px;
                    }

                    .icon:before {
                        position: relative;
                        top: 3px;
                        margin-right: 8px;
                        font-size: 16px;
                    }
                }

                .status {
                    margin-top: 40px;
                    font: normal 12px robotobold_italic;
                    color: #c4cece;

                    &.yellow {
                        color: #febe10;
                    }

                    &.green {
                        color: #1db670;
                    }

                    &.red {
                        color: #ed272f;
                    }
                }
            }
        }
    }

    .footer {
        background: @background-container;
        height: @heightFooter;
        position: relative;

        div {
            top: 30%;
        }

        a {
            padding: 5px 0;
        }
    }
}
