@import '../../../less/controls/variables.less';

.bg-gradient(@topColor, @bottomColor) {
    background: @topColor; /* Old browsers */
    background: -moz-linear-gradient(top, @topColor 0%, @bottomColor 100%); /* FF3.6+ */
    background: -webkit-gradient(linear, left top, left bottom, color-stop(0%,@topColor), color-stop(100%,@bottomColor)); /* Chrome,Safari4+ */
    background: -webkit-linear-gradient(top, @topColor 0%,@bottomColor 100%); /* Chrome10+,Safari5.1+ */
    background: -o-linear-gradient(top, @topColor 0%,@bottomColor 100%); /* Opera 11.10+ */
    background: -ms-linear-gradient(top, @topColor 0%,@bottomColor 100%); /* IE10+ */
    background: linear-gradient(to bottom, @topColor 0%,@bottomColor 100%); /* W3C */
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr=@topColor, endColorstr=@bottomColor,GradientType=0 ); /* IE6-9 */
}
.main-menu {
    border-top: 5px solid @BorderColor;
    padding: 0;
    height: 100%;
    position: fixed;
    top: 0;
    left: 0;
    width: @MenuWidth;
    z-index: 200;
    transition: left 0.3s ease-out;
    overflow-y: auto;
    -ms-overflow-style: none;
    padding-bottom: 20px;
    overflow: hidden;
    .bg-gradient(@default-blue-color, @default-green-color);
    .main-menu__logo {
        margin-top: 30px;
        display: block;
        height: 75px;
        width: 100%;
        min-width: 160px;
        background: url(../images/logo.png) center center no-repeat;
        margin-bottom: 40px;
    }

    .main-menu__items {
        padding: 0;
        margin: 0;
    }
}

.toggle-menu {
    display: none;
}

@media (max-width: 1200px) {
    .main-menu {
        left: -@MenuWidth;
    }
    .menu-visible {
        left: 0;
    }
    .toggle-menu {
        display: block !important;
        left: 5px;
        top: 10px;
        position: fixed;
        z-index: 200;
        transition: left 0.3s ease-out;
        .icon {
            opacity: 0.6;
            font-size: 26px;
        }
    }
    .toggle-menu-visible {
        left: @MenuWidth + 5px !important;
    }
}
