/*tabs*/
@tabs-top-active-color: #ff5e1d;
@tabs-active-color: #ffffff;
@tabs-border-color: #d0d0d0;
@tabs-background-color: #f4f4f4;

.invalidStyle {
    border: 0px solid #ed272f;
    outline: 0;
    box-shadow: inset 0 1px 1px rgba(0,0,0,.075), 0 0 12px #ed272f;
    -moz-box-shadow: inset 0 1px 1px rgba(0,0,0,.075), 0 0 8px #ed272f;
    -webkit-box-shadow: inset 0 1px 1px rgba(0,0,0,.075), 0 0 8px #ed272f;
}

input.ng-invalid.ng-dirty {
    .invalidStyle();
}

.show-error-after-submit {
    .ng-invalid:not(.no-style-error) {
        .invalidStyle();
    }

    .multiSelect.ng-invalid:not(.no-style-error) {
        .multiSelectButton {
            .invalidStyle();
        }
    }
}

.col-center > [class*="col-"] {
    float: none;
    margin-left: auto;
    margin-right: auto;
}

.action-icon {
    .icon {
        &:before {
            vertical-align: middle;
            top: 4px;
        }
    }
}

button {
    transition: border-color ease-in-out .15s, box-shadow ease-in-out .15s;

    &:focus {
        border-color: #66afe9;
        outline: 0;
        -webkit-box-shadow: inset 0 1px 1px rgba(0,0,0,.075), 0 0 8px rgba(102, 175, 233, 0.6);
        box-shadow: inset 0 1px 1px rgba(0,0,0,.075), 0 0 8px rgba(102, 175, 233, 0.6);
    }
}
.label-padding {
    padding-top: 20px;
}
.blue {
    color: @ath-color-skyBlue;
    transition: all 0.2s linear;

    &:hover {
        color: #74cbc8;
    }
}

.ath-setting-type {
    .form-control {
        transition: none;
    }
}

.build-info {
    font-size: 10px;
    margin-top: 20px;
    background-color: #74cbc8;
    color: white;
    padding: 0 25px;
}

.checkbox-wrapper {
    @widthHeight-xs: 16px;
    @widthHeight-sm: 20px;
    @widthHeight-md: 30px;
    @widthHeight-lg: 40px;
    @paddingSpan: 10px;
    @topLeft-xs: 0px;
    @topLeft-sm: 2px;
    @topLeft-md: 8px;
    @topLeft-lg: 12px;
    @vertical-xs: 0px;
    @vertical-sm: 5px;
    @vertical-md: 10px;
    @vertical-lg: 15px;

    .checkboxSetting(@widthHeight, @padding, @top, @left, @vertical) {
        label {
            font: normal 14px robotoregular;

            .icon-checkbox {
                width: @widthHeight;
                height: @widthHeight;

                &:before {
                    position: absolute;
                    top: @top;
                    left: @left;
                }
            }


            .label-checkbox {
                vertical-align: @vertical;

                &.padding-checkbox-left {
                    padding-left: @padding;
                }

                &.padding-checkbox-right {
                    padding-right: @padding;
                }
            }
        }
    }

    &.checkbox-xs {
        .checkboxSetting(@widthHeight-xs, @paddingSpan, @topLeft-xs, @topLeft-xs, @vertical-xs);
    }

    &.checkbox-sm {
        .checkboxSetting(@widthHeight-sm, @paddingSpan, @topLeft-sm, @topLeft-sm, @vertical-sm);
    }

    &.checkbox-md {
        .checkboxSetting(@widthHeight-md, @paddingSpan, @topLeft-md, @topLeft-md, @vertical-md);
    }

    &.checkbox-lg {
        .checkboxSetting(@widthHeight-lg, @paddingSpan, @topLeft-lg, @topLeft-lg, @vertical-lg);
    }

    input {
        display: none;
    }

    .checkboxSetting(@widthHeight-md, @paddingSpan, @topLeft-md, @topLeft-md, @vertical-md);

    label {
        cursor: pointer;

        .icon-checkbox {
            display: inline-block;
            border: 1px solid #c9dee1;
            position: relative;
            border-radius: 2px;
            background: white;
            color: transparent;
            cursor: pointer;
            transition: background-color 0.15s ease-out;

            &:before {
                position: absolute;
            }
        }


    }

    input:checked + label > .icon-checkbox {
        color: white;
        background: @default-green-color;
    }
}

.form-control[readonly] {
    background-color: white;
    cursor: pointer;
}

.radio-wrapper {
    input {
        display: none;
    }

    label {
        font: normal 14px/36px robotoregular;

        .icon {
            display: inline-block;
            border: 2px solid #c9dee1;
            width: 30px;
            height: 30px;
            position: relative;
            margin-right: 10px;
            border-radius: 30px;
            background: white;
            color: white;

            .radio-item {
                width: 15px;
                height: 15px;
                border-radius: 15px;
                background-color: white;
                top: 0.4em;
                position: absolute;
                left: 0.4em;
            }
        }
    }

    input:checked + label .icon .radio-item {
        width: 15px;
        height: 15px;
        border-radius: 15px;
        background-color: @default-green-color;
        top: 0.4em;
        position: absolute;
        left: 0.4em;
    }
}
///icon
.animate-pointer {
    transition: all 0.3s ease-in-out;

    &.active-pointer {
        transform: rotate(180deg);
    }
}

///

textarea.form-control, input.form-control, .tags input {
    &.placeholder {
        opacity: 1;
        transition: opacity @time-opacity-placeholder ease;
        color: @color-placeholder;
    }

    &::-webkit-input-placeholder {
        .placeholder;
    }

    &::-moz-placeholder {
        .placeholder;
    }

    &::-moz-placeholder {
        .placeholder;
    }

    &:-moz-placeholder {
        .placeholder;
    }

    &:-ms-input-placeholder {
        .placeholder;
    }

    &:focus {
        &::-webkit-input-placeholder {
            opacity: 0;
        }

        &::-moz-placeholder {
            opacity: 0;
        }

        &:-moz-placeholder {
            opacity: 0;
        }

        &:-ms-input-placeholder {
            opacity: 0;
        }
    }
}

@width-dp-button: 38px;

.datepicker-wrapper {
    position: relative;

    &:after {
        content: 'g';
        position: absolute;
        top: 11px;
        right: 11px;
        .base-icon-after()
    }
}

.datepicker-wrapper--botton {
    &:after {
        content: '';
    }
    .datepicker-wrapper--botton__remove-button {
        width: @width-dp-button;
        height: @width-dp-button;
        float: left;
        border: 1px solid #c7dcdf;
        border-radius: 0;
        border-left: none;
    }
    .datepicker-wrapper--botton__imput {
        width: -moz-calc( ~"100% - "@width-dp-button);
        width: -webkit-calc( ~"100% - "@width-dp-button);
        width: -o-calc( ~"100% - "@width-dp-button);
        width: calc( ~"100% - "@width-dp-button);
        float: left;
    }
    
    .datepicker-wrapper--botton__imput--with-remove{
        width: -moz-calc( ~"100% - "@width-dp-button);
        width: -webkit-calc( ~"100% - "@width-dp-button);
        width: -o-calc( ~"100% - "@width-dp-button);
        width: calc( ~"100% - "@width-dp-button*2 );
        float: left;
    }

    .datepicker-wrapper--botton__icon {
        width: @width-dp-button;
        height: @width-dp-button;
        float: left;
        border: 1px solid #c7dcdf;
        border-radius: 0;
        border-left: none;
        &:focus {
            background-color: transparent !important;
        }
        &:after {
            content: 'g';
            position: absolute;
            top: 9px;
            right: 9px;
            .base-icon-after()
        }
    }
}

#tabs {
    ul {
        list-style: none;
        margin-bottom: 0;

        &.tabs {
            display: block;
            height: 43px;
            border-bottom: 1px solid @tabs-border-color;

            li {
                display: block;
                float: left;
                margin-right: 9px;
                background: @tabs-background-color;
                border-top: 2px solid @tabs-background-color;
                border-bottom: 1px solid @tabs-border-color;
                padding: 0;

                &.active {
                    border-top: 2px solid @tabs-top-active-color;
                }

                &.active span {
                    color: @tabs-top-active-color;
                    border-left: 1px solid @tabs-border-color;
                    border-right: 1px solid @tabs-border-color;
                }

                &.active.white {
                    background: @tabs-active-color;
                    border-bottom: 1px solid @tabs-active-color;
                }

                &.active.gray {
                    border-bottom: 1px solid @tabs-background-color;
                }

                span {
                    display: block;
                    color: #0774c1;
                    border-left: 1px solid @tabs-background-color;
                    border-right: 1px solid @tabs-background-color;
                    padding: 10px 20px 10px;
                    cursor: pointer;
                }
            }
        }
    }
}

.tab-body {
    background: @tabs-background-color;
}

.block-tabs {
    padding: 0 15px 0px;
}

.v-middle {
    display: inline-block;
    vertical-align: middle;
}

.time-selector {
    float: left;
    position: relative;
    display: block;

    label {
        position: relative;
        display: inline-block;
        font-size: 10px;
        margin: 4px 3px 0 0;
        padding: 0;
        line-height: normal;
        overflow: visible;
        text-align: center;
        vertical-align: middle;
        cursor: pointer;
        width: 14px;
        height: 15px;

        span {
            display: block;
            color: #000000;
            background: #ffffff;
            width: 14px;
            height: 15px;
        }

        input:checked + label span {
            color: #ffffff;
            background: #929292;
        }
    }

    input {
        border: 0 none;
        clip: rect(0, 0, 0, 0);
        height: 1px;
        margin: -1px;
        overflow: hidden;
        padding: 0;
        position: absolute;
        width: 1px;
    }
}
